.page-header {
  min-height: 878px;
  background: url('../img/black-storm.png') #1b2d37;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  box-sizing: border-box;
  padding-left: 15px;
  padding-right: 15px;
}


@media (max-width: $tablet-max-width) {
  .page-header {
    padding-left: 25px;
    padding-right: 25px;
  }
}

@media (max-width: $mobile-max-width) {
  .page-header {
    padding-left: 15px;
    padding-right: 15px;
  }
}
